.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  margin: 0 auto;
  font-family: 'QuickSand', sans-serif;
  font-size: 1em;
  color: black;
  
}

.inputsCont {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 10px;
}

p {
  width: 20px;
  margin: 0;
  font-weight: 700;
}

.inputContent {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  min-width: 100px;
  margin: 0 auto;
  align-items: left;
  justify-content: center;
  gap: 15px;
}